.Player {
  -webkit-filter: drop-shadow(0px 0px 2px rgba(0,0,0,0.5));
          filter: drop-shadow(0px 0px 2px rgba(0,0,0,0.5));
  height: 8vw;
  pointer-events: all;
  position: absolute;
  width: 8vw;
}

.Player.GK .Player-background {
  -webkit-filter: invert(100%) hue-rotate(60deg);
          filter: invert(100%) hue-rotate(60deg);
}

.Player-background {
  cursor: move;
  height:100%;
  width:100%;
}

.Player-label {
  font-size: 1.2vw;
  position: relative;
  text-align: center;
  text-shadow:
    1px 1px 0px #000, 1px -1px 0px #000,
    -1px 1px 0px #000, -1px -1px 0px #000;
  top: -6.5vw;
}

.Player-label input {
  width: 2vw;
}



